<template>
  <div class="col-sm-12 col-md-6 col-lg-4">
    <div class="card border-dark mb-3">
      <div class="card-header">
        {{stock.name}}
        <small>(price: {{stock.price}})</small>
      </div>
      <div class="card-body">
        <div class="d-flex justify-content-between">
          <input type="number" placeholder="0" class="form-control mr-2" v-model="quantity" />
          <button
            class="btn btn-dark"
            @click="buyStock"
            :disabled="Number(this.quantity) <= 0 ||  !Number.isInteger(Number(this.quantity)) || this.funds < (this.quantity * stock.price) "
          >Buy</button>
        </div>
      </div>
    </div>
    <br />
  </div>
</template>

<script>
export default {
  props: ["stock"],
  methods: {
    buyStock() {
      const order = {
        id: this.stock.id,
        quantity: Number(this.quantity),
        price: this.stock.price,
        buyer: localStorage.getItem("userId"),
      };
      console.log(order);
      this.$store.dispatch('buyStocks',order)
      this.quantity = null;
    }
  },
  data() {
    return {
      quantity: null
    };
  },
  computed:{
    funds(){
      return this.$store.getters.funds;
    }
  }
};
</script>
