import axiosData from '../../axios/axios-data'

const state = {
    stocks: [],
    funds: null
}

const mutations = {
    initFunds(state) {
        axiosData.get("/users/" + localStorage.getItem("userId") + "/" + 'funds' + ".json" + "?auth=" + localStorage.getItem("idToken"))
            .then(res => {
                state.funds = res.data;
            });
    },
    setStocks(state) {
        console.log("SET STOCKS!")
        axiosData.get('/stocks.json?auth=' + localStorage.getItem('idToken'))
            .then(res => {
                state.stocks = res.data;
                axiosData.get('/users/' + localStorage.getItem("userId") + '/stocks' + '.json' + '?auth=' + localStorage.getItem('idToken'))
                    .then(res => {
                        const loadQuantity = res.data;
                        for (let id in state.stocks) {
                            state.stocks[id].quantity = loadQuantity === null ? 0 : loadQuantity[id];
                        }
                    })
            })
    },
    buyStocks(state, { id, quantity, price, buyer }) {
        console.log("BUY STOCKS");
        state.stocks[id].quantity += quantity;
        state.funds -= (quantity*price);
        console.log(JSON.stringify(state.stocks[id].quantity));
        axiosData.put('/users/' + localStorage.getItem("userId") + '/stocks/' + id + '.json' + '?auth=' + localStorage.getItem('idToken'),state.stocks[id].quantity);
        axiosData.put("/users/" + localStorage.getItem("userId") + "/" + 'funds' + ".json" + "?auth=" + localStorage.getItem("idToken"),state.funds);
    },
    sellStocks(state, { id, quantity, price, buyer }) {
        console.log("SELL STOCKS");
        state.stocks[id].quantity -= quantity;
        state.funds += (quantity*price);
        console.log(JSON.stringify(state.stocks[id].quantity));
        axiosData.put('/users/' + localStorage.getItem("userId") + '/stocks/' + id + '.json' + '?auth=' + localStorage.getItem('idToken'),state.stocks[id].quantity);
        axiosData.put("/users/" + localStorage.getItem("userId") + "/" + 'funds' + ".json" + "?auth=" + localStorage.getItem("idToken"),state.funds);
    },
    rndStocks(state) {
        for(let id in state.stocks){
            state.stocks[id].price = Math.round(state.stocks[id].price + (Math.random() * 10))
            axiosData.put("stocks/"+id+"/price.json" + "?auth=" + localStorage.getItem("idToken"), state.stocks[id].price)
        }
    }
}

const actions = {
    initStocks({ commit }) {
        commit('setStocks');
    },
    initFunds({ commit }) {
        commit('initFunds');
    },
    rndStocks({ commit }) {
        commit('rndStocks')
    },
    buyStocks({ commit }, order) {
        commit('buyStocks', order);
    },
    sellStocks({ commit }, order) {
        commit('sellStocks', order);
    },
}

const getters = {
    stocks(state) {
        return state.stocks
    },
    funds(state) {
        return state.funds;
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
}

