<template>
  <div v-if="stock.quantity > 0" class="col-sm-12 col-md-6 col-lg-4">
    <div class="card border-dark mb-3">
      <div class="card-header">
        {{stock.name}}
        <small>(price: {{stock.price}} | Quantity: {{stock.quantity}})</small>
      </div>
      <div class="card-body">
        <div class="d-flex justify-content-between">
          <input type="number" placeholder="0" class="form-control mr-2" v-model="quantity" />
          <button
            class="btn btn-dark"
            @click="sellStock"
            :disabled="Number(this.quantity) <= 0 ||  !Number.isInteger(Number(this.quantity)) || stock.quantity < this.quantity"
          >Sell</button>
        </div>
      </div>
    </div>
    <br />
  </div>
</template>

<script>
export default {
  props: ["stock"],
  methods: {
    sellStock() {
      const order = {
        id: this.stock.id,
        quantity: Number(this.quantity),
        price: this.stock.price,
        buyer: localStorage.getItem("userId")
      };
      console.log(order);
      this.$store.dispatch("sellStocks", order);
      this.quantity = null;
    }
  },
  data() {
    return {
      quantity: null
    };
  }
};
</script>
