import axiosAuth from '../../axios/axios-auth'
import axiosData from '../../axios/axios-data'
import router from '../../router'

import Vue from 'vue'
import VueSimpleAlert from "vue-simple-alert";
Vue.use(VueSimpleAlert);


const state = {
    idToken: null,
    userId: null,
    email: null,
    user: null,
}

const mutations = {
    authUser(state, userData) {
        state.idToken = userData.token
        state.email = userData.email
    },
    storeUser(state, user) {
        state.user = user
    },
    clearAuth(state) {
        state.idToken = null
        state.email = null
        state.user = null
    }
}

const actions = {
    signup({ commit, dispatch }, authData) {
        axiosAuth.post(`/accounts:signUp?key=${process.env.VUE_APP_FIREBASE_KEY}`, {
            email: authData.email,
            password: authData.password,
            returnSecureToken: true
        })
            .then(res => {
                console.log("SIGNUP")
                const now = new Date();
                const expireDate = new Date(now.getTime() + res.data.expiresIn * 1000);
                localStorage.setItem('idToken', res.data.idToken);
                localStorage.setItem('expireDate', expireDate);
                localStorage.setItem('email', authData.email);
                commit('authUser', {
                    token: res.data.idToken,
                    email: authData.email
                })
                dispatch('storeUser', authData)
                // setTimeout(() => {
                // dispatch('fetchUser')
                dispatch('setLogoutTimer', res.data.expiresIn)
                router.replace('/')
                // setTimeout(() => {
                // dispatch('initFunds')
                // dispatch('initStocks')
                // router.replace('/')
                // }, 500)
                // }, 500)
            })
            .catch(error => {
                console.log(error);
                Vue.alert('Please try again', "ERROR!", "error")
            })
    },
    signin({ commit, dispatch }, authData) {
        axiosAuth.post(`/accounts:signInWithPassword?key=${process.env.VUE_APP_FIREBASE_KEY}`, {
            email: authData.email,
            password: authData.password,
            returnSecureToken: true
        })
            .then(res => {
                console.log("SIGNIN")
                const now = new Date();
                const expireDate = new Date(now.getTime() + res.data.expiresIn * 1000);
                localStorage.setItem('idToken', res.data.idToken);
                localStorage.setItem('expireDate', expireDate);
                localStorage.setItem('email', authData.email);
                commit('authUser', {
                    token: res.data.idToken,
                    email: authData.email
                })
                // setTimeout(() => {
                dispatch('fetchUser')
                dispatch('setLogoutTimer', res.data.expiresIn)
                router.replace('/')
                // setTimeout(() => {
                // dispatch('initFunds')
                // dispatch('initStocks')
                // router.replace('/')
                // }, 500)
                // }, 500)

            })
            .catch(error => {
                console.log(error)
                Vue.alert('Please try again', "ERROR!", "error")
            })
    },
    storeUser({ state, dispatch }, userData) {
        if (!state.idToken) {
            return
        }
        axiosData.post('/users.json' + '?auth=' + state.idToken, {
            email: userData.email,
            funds: 50000
        })
            .then(res => {
                // dispatch('setLogoutTimer', res.data.expiresIn)
            })
            .catch(err => console.log(err))
    },
    setLogoutTimer({ dispatch }, timer) {
        setTimeout(() => {
            dispatch('logout');
            router.replace('/signin')
        }, timer * 1000)
    },
    logout({ commit }) {
        commit('clearAuth');
        localStorage.removeItem('expireDate');
        localStorage.removeItem('idToken');
        localStorage.removeItem('userId');
        localStorage.removeItem('email');
        router.replace('/signin')
    },
    autoLogin({ commit, dispatch }) {
        const token = localStorage.getItem('idToken');
        if (!token) {
            return
        }
        const expireDate = localStorage.getItem('expireDate');
        const now = new Date();
        if (now >= expireDate) {
            return
        }
        const email = localStorage.getItem('email');
        commit('authUser', {
            token: token,
            email: email
        })

        dispatch('fetchUser')
    },
    fetchUser({ commit, state, dispatch }) {
        if (!state.idToken) {
            console.log("Cant find Token");
            return
        }
        axiosData.get('/users.json' + '?auth=' + state.idToken)
            .then(res => {
                const data = res.data
                const chEmail = localStorage.getItem('email');
                let users = null;
                for (let key in data) {
                    const user = data[key];
                    if (chEmail == user.email) {
                        user.id = key;
                        localStorage.setItem('userId', key);
                        users = user;
                    }
                }
                console.log("USER:" + JSON.stringify(users))
                commit('storeUser', users)
            })
            .then(() => {
                console.log("After fetching");
                dispatch("initStocks");
                dispatch("initFunds");
            })
            .catch(error => console.log(error))
    }
}

const getters = {
    user(state) {
        return state.user
    },
    isAuth(state) {
        return state.idToken != null;
    },
    token(state) {
        return state.idToken;
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
}
